.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.header-top {
  padding: 5px 0; }
  .header-top .container {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center; }
  .header-top .holder-form {
    -webkit-box-flex: 1.85;
    -webkit-flex-grow: 1.85;
    -ms-flex-positive: 1.85;
    flex-grow: 1.85;
    text-align: center; }
  .header-top .holder-plan {
    -webkit-flex-basis: 270px;
    -ms-flex-preferred-size: 270px;
    flex-basis: 270px;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
