.header-bottom {
  background: #662d91;
  margin: 0 0 55px; }
  .header-bottom .container {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    padding-bottom: 15px;
    padding-top: 15px; }
  .header-bottom .logo {
    position: absolute;
    background: url("../img/logo.png") no-repeat;
    width: 186px;
    height: 95px;
    font-size: 0;
    line-height: 0;
    top: 0;
    left: 10px; }
  .header-bottom .holder-nav {
    padding: 0 0 0 190px;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
    width: 100%; }
  .header-bottom .nav {
    font-family: 'Conv_AgendaMedium', Helvetica, sans-serif;
    font-size: 21px;
    text-transform: uppercase; }
  .header-bottom .nav-menu li {
    display: inline-block;
    vertical-align: middle;
    padding: 0 25px;
    position: relative; }
    .header-bottom .nav-menu li:after {
      content: '|';
      position: absolute;
      right: 0;
      top: 42%;
      -webkit-transform: translateY(-42%);
      -ms-transform: translateY(-42%);
      transform: translateY(-42%);
      color: #ed8e53;
      font-size: 15px; }
    .header-bottom .nav-menu li:last-child:after {
      content: ''; }
  .header-bottom .nav-menu a {
    color: #fff; }
  .header-bottom .holder-book {
    font-family: 'Conv_AgendaBold', Helvetica, sans-serif;
    font-size: 18px;
    text-transform: uppercase;
    padding: 0 0 0 38px; }
  .header-bottom .book {
    color: #fff;
    position: relative; }
    .header-bottom .book:before {
      content: '\e900';
      font-family: 'icomoon' !important;
      font-size: 24px;
      position: absolute;
      top: 50%;
      left: -38px;
      -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
      opacity: 0.5; }

